<template>
<Panel :title="title">
<Navbar :items="items1" :newName="newClass1"></Navbar>
<Navbar :items="items2" :newName="newClass2"></Navbar>
</Panel>
</template>
<script>
import Panel from "../core/Panel"
import Navbar from "../core/Navbar"
export default {
  name:"Insurance",
  components:{
    Panel,
    Navbar
  },
  data(){
    return{
      title:"有车有钱",
      newClass1:"home-mix-banner",
      newClass2:"home-mix-nav",
      items1:[
        {src:"./static/mix-1.png",alt:"开新车，去踏青",title:"",link:"/home"},
        {src:"./static/mix-2.jpg",alt:"最高可贷50万元",title:"",link:"/home"}
      ],
      items2:[
        {src:"./static/mix-icon-1.png",alt:"旅游",title:"旅游",link:"/home"},
        {src:"./static/mix-icon-2.png",alt:"医美",title:"医美",link:"/home"},
        {src:"./static/mix-icon-3.png",alt:"房抵押",title:"房抵押",link:"/home"},
        {src:"./static/mix-icon-4.png",alt:"驾校",title:"驾校",link:"/home"},
        {src:"./static/mix-icon-5.png",alt:"分期用",title:"分期用",link:"/home"}
      ]
    }
  }
}
</script>
<style lang="less" type="text/less" scoped>
.banner{
display: block;
width: 100%;
 }

</style>



