<template>
  <footer class="public-foot">
    <Navbar :items="items"></Navbar>
    <p>
      Copyright © 2004-2018 京东JD.com 版权所有
    </p>
    <p>
      投资有风险，购买需谨慎
    </p>
    <p>
      京东金融平台服务协议
    </p>
    <p>
      京东金融隐私政策
    </p>
    <Navbar  :items="items2"  class="bottom-nav" :newName="newName"></Navbar>
  </footer>
</template>
<script>
import Navbar from "../core/Navbar"
export default {
  name:"Footer",
components:{
  Navbar
},
created(){
  this.datas.forEach(item=>this.old.push(item.src))
},
computed:{
  items2:function(){
    this.index=this.path.indexOf(this.$route.path)

    this.old.forEach((item,num)=>{
      if(this.index==num){

    this.datas[num].src=this.new[num];
      }else{
        this.datas[num].src=item
      }
    })

    return this.datas;
  }
},
  data(){
    return{
      index:"",
      path:["/","/money","/debit","/mine"],
      newName:"bottom-nav",
      old:[],
      new:["./static/home2.png","./static/money2.png","./static/debit2.png","./static/home2.png"],
      datas:[
        {
          src:"./static/home.png",
          alt:"首页",
          title:"首页",
          link:"/"
        },
        {
          src:"./static/money.png",
          alt:"理财",
          title:"理财",
          link:"/money"
        },
        {
          src:"./static/debit.png",
          alt:"白条",
          title:"白条",
          link:"/debit"
        },
        {
          src:"./static/mine.png",
          alt:"我的",
          title:"我的",
          link:"/mine"
        }
      ],
      items:[
        {
          src:"./static/foot-nav1.png",
          alt:"客户端",
          title:"客户端",
          link:"/la"
        },
        {
          src:"./static/foot-nav2.png",
          alt:"触屏版",
          title:"触屏版",
          link:"/la"
        },
        {
          src:"./static/foot-nav3.png",
          alt:"电脑版",
          title:"电脑版",
          link:"/la"
        },
      ]
    }
  }
}
</script>
<style lang="less" type="text/less" scoped>
@import "../../style/element";
.public-foot{
margin-bottom:100px;
margin-top:40px;
p{
text-align: center;
color:#999;
line-height: 1.6;
font-size: 24px;
padding: 24px 32px;
position: relative;
box-sizing: border-box;
  &::before{
content: "";
width: 100%;
height: 1px;
background-color: #ddd;
transform: scaleY(.5);
position: absolute;
left: 0;
top:0;
  }
}
.bottom-nav{
position: fixed;
bottom: 0;
left: 0;
width: 100%;
padding:0;
background-color: #fff;
}
}
</style>



