<template>
  <Panel :title="title">
    <section class="product-choice">
      <div v-for="product in products" class="product">
        <img class="product-icon" :src="product.src"/>
        <h4 class="product-title">{{product.name}}</h4>
        <p class="product-feature">
          {{product.feature}}
        </p>
        <router-link class="product-links" tag="a" :to="product.link">去赚钱</router-link>


      </div>
    </section>
  </Panel>
</template>
<script>
import Panel from "../core/Panel"
export default {
  name:"Financing",
  components:{
    Panel
  },
  data(){
    return{
      title:"理财精选",
      products:[
        {
          name:"月度理财",
          feature:"预期年化收益率4.50%",
          src:"./static/money-icon1.png",
          link:"/"
        },
        {
          name:"季度理财",
          feature:"历史年化回报率5.50%",
          src:"./static/money-icon2.png",
          link:"/"
        },
        {
          name:"一年理财",
          feature:"历史年化回报率5.80%",
          src:"./static/money-icon3.png",
          link:"/"
        }

      ]
    }
  }
}
</script>

<style lang="less" type="text/less" scoped>
.bold(){font-weight: 700}
.product-choice{
  position: relative;
      &::before{
        content: "";
        width: 100%;
        position: absolute;
        top: 0;
        height: 1px;
        background-color: #eee;
    }
  .product{
    position: relative;
    padding: 0 32px 0 152px;
    height: 140px;
    &::before{
      content: "";
      width: 100%;
      height: 1px;
      background-color: #eee;
      display: block;
      transform: scaleY(.5);
    }
    .product-icon{
      position: absolute;
      height: 88px;
      width: 88px;
      left: 32px;
      top:26px;
    }
    .product-title{
      font-size: 32px;
      color:#333;
      line-height: 1.8;
      padding-top:26px;
    }
    .product-feature{
        color: #ff3232;
        font-size: 24px;
        max-width: 48%;
        white-space: nowrap;
        text-overflow: ellipsis;
        overflow: hidden;
    }
    .product-links{
      position: absolute;
      right:32px;
      font-size: 28px;
      top: 0;
      line-height: 140px;
      color:#ff801a;
      padding-right:36px;
      background: url("~@/assets/icon_arrow1.png") no-repeat right center;
    }
}

}
</style>



