<template>
<Panel :title="title">
<Navbar :items="items" :newName="newClass"></Navbar>
</Panel>
</template>
<script>
import Panel from "../core/Panel"
import Navbar from "../core/Navbar"
export default {
  name:"Insurance",
  components:{
    Panel,
    Navbar
  },
  data(){
    return{
      title:"精品活动",
      newClass:"home-mix-banner",
      items:[
        {src:"@/assets/debit-middle-banner1.png",alt:"开新车，去踏青",title:"",link:"/home"},
        {src:"@/assets/debit-middle-banner2.png",alt:"最高可贷50万元",title:"",link:"/home"}
      ]
    }
  }
}
</script>
<style lang="less" type="text/less" scoped>
</style>



