    <img  src="../../../static/money-middle-banner.png"/>
  </router-link>
  <Financing></Financing>
</div>
</template>
<script>
import Slide from "./Slide"
import Intro from "./Intro"
import Financing from "./Financing"

export default {
  name:"Money",
  components:{
    Slide,
    Intro,
    Financing
  },
  data(){
    return{

    }
  }
}
</script>
<style lang="less" type="text/less" scoped>
.money{
margin-top:120px;
.money-header-banner{
  display: block;
  width: 100%;
  background-color: #fff;
}
.money-middle-banner{
  display: block;
  margin-top: 20px;
  img{
    width: 100%
  }
}
}

</style>



