<template>
<div class="login">
<header class="login-header">
  <span @click="history" class="go-back"></span>
  <h4 class="login-header-title">京东登陆</h4>
</header>
<section class="login-chunck">
  <div class="login-dt">
    <div>
      <span @click="change(true)" :class="{active:isAccount}">账号密码登陆</span>
    </div>
    <div>
      <span @click="change(false)" :class="{active:!isAccount}">短信验证码登陆</span>
    </div>

  </div>
</section>
<section class="login-wrap">
  <keep-alive>
    <div class="login-dd-item" v-if="isAccount">
      <div class="login-dd-chunk">
        <label for="login-account">账号</label>
        <input id="login-account" type="text" v-model="account" placeholder="用户名/邮箱/已验证手机号"/>
      </div>
      <div class="login-dd-chunk">
        <label for="login-password">密码</label>
        <input id="login-password" type="password" v-model="password" placeholder="请输入密码" ref="password"/>
        <span class="check-type" @click="changeType"></span>
      </div>
    </div>
    <div class="login-dd-item" v-else>
      <div class="login-dd-chunk">
        <input placeholder="请输入手机号" type="tel" maxlength=11 class="tel-test" />
        <router-link class="get-test" tag="span" to="/404">获取验证码</router-link>

      </div>
      <div class="login-dd-chunk">
        <input placeholder="请输入收到的验证码" type="text" maxlength="4"/>
      </div>

    </div>
    </keep-alive>
    <router-link class="login-btn" tag="a" to="/404">登陆</router-link>
    <router-link class="login-quick" tag="a" to="/404">一键登录</router-link>


    <div class="quick-nav">
      <a class="quick-reg" href="/404">手机快速注册</a>
      <a class="forget-password" href="/404">忘记密码</a>

    </div>
      <div class="other-login">
        <h4>
          其他登陆方式
        </h4>
        <a class="qq" href="/404"></a>
        <a class="weixin" href="/404"></a>
        <p>
          登陆即代表您已同意京东隐私政策
        </p>
      </div>
</section>
</div>
</template>
<script>
export default {
  name:"Login",
  methods:{
    change(boolean){
      this.isAccount=boolean;
    },
    history(){
      console.log(window.history)
      window.history.go(-1)
    },
    changeType(){
      let type=this.$refs.password.type
      this.$refs.password.type=type==="password"?"text":"password";
      console.log(this.$refs.password.type)
    }
  },
  data(){
    return{
      account:"",
      password:"",
      isAccount:"true"
    }
  }
}
</script>
<style lang="less" type="text/less" scoped>
.login{
  background-color: #fff;
  .login-header{
    position: relative;
    .go-back{
      position: absolute;
      width: 44px;
      height: 44px;
      left: 24px;
      top:24px;
      background: url("./static/iconBg.png") 0 -126px no-repeat;
    }
    .login-header-title{
      text-align: center;
      background-color: #f9f9f9;
      line-height: 90px;
      font-size: 34px;
      border-bottom: 1px solid #bbb;
      color:#616161;
    }
  }
  .login-chunck{
    .login-dt{
      border-bottom: 1px solid #d7d7d7;
      line-height: 88px;
      height: 88px;
      font-size: 28px;
      >div{
        float: left;
        width: 50%;
        text-align: center;
        >span{
          line-height: 88px;
          display: inline-block;
          height: 88px;
          box-sizing: border-box;
          &.active{
            border-bottom: 2px solid #f23030;
          }
        }
        .active{
          border-bottom: 2px solid #f23030;
        }
      }
    }
}
  .login-wrap{
    max-width: 1500px;
    padding: 0 40px 40px 40px;
    margin: 0 auto;
    .login-dd-item{
      position: relative;
      .login-dd-chunk{
        border-bottom: 1px solid #d7d7d7;
        position: relative;
        label{
          position: absolute;
          line-height: 96px;
          font-size: 28px;
        }
        input{
          width: 100%;
          padding: 0 60px 0 120px;
          box-sizing: border-box;
          font-size: 28px;
          border:none;
          line-height: 96px;
        }
        .check-type{
          position: absolute;
          width: 60px;
          height: 36px;
          right: 0;
          top: 30px;
          background:url("./static/hide_password.png") no-repeat;
        }
          .tel-test{
            padding-right: 260px;
          }
        .get-test{
          position:absolute;
          width:232px;
          border-left: 1px solid #d7d7d7;
          line-height: 96px;
          right: 0;
          top: 0;
          color: #848689;
          font-size: 28px;
          text-align:center;
        }
      }
    }
    .login-btn{
      display: block;
      line-height: 90px;
      width: 100%;
      text-align:center;
      color:#848689;
      background-color: #eee;
      border-radius: 4px;
      font-size: 28px;
      margin-top: 20px;
    }
    .login-quick{
      @red:#f23030;
      .login-btn;
      background-color: #fff;
      border: 1px solid @red;
      color:@red;
    }
  }
  .quick-nav{
    a{
      color:#333;
      display: inline-block;
      padding-left: 44px;
      line-height: 44px;
      background: url("./static/iconBg.png") no-repeat 0 -46px;
    }
    font-size: 24px;
    margin-top: 40px;
    margin-bottom:210px;
      .quick-reg{
        float: right;
        background-position: 0 -84px;
      }
  }
  .other-login{
    text-align: center;
    border-top: 1px solid #d7d7d7;
    padding-top: 120px;
    position: relative;
    >h4{
      text-align: center;
      color: #bfbfbf;
      font-size: 28px;
      padding: 45px 20px 0 20px;
      display: inline-block;
      background-color: #fff;
      position: relative;
      top: -180px;
    }
    .qq{
      position: absolute;
      left: 50%;
      top:100px;
      margin-left: -150px;
      width: 44px;
      height: 44px;
      background: url("./static/iconBg.png") no-repeat 0 -258px;
    }
    .weixin{
      .qq;
      background-position: 0 -310px;
      margin-left: 100px;
    }
    p{
      font-size: 28px;
    }
  }
}

</style>



