<template>
<Panel :title="title">
<Navbar :items="items1" :newName="newClass"></Navbar>
<Navbar :items="items2" :newName="newClass"></Navbar>
</Panel>
</template>
<script>
import Panel from "../core/Panel"
import Navbar from "../core/Navbar"
export default {
  name:"Insurance",
  components:{
    Panel,
    Navbar
  },
  data(){
    return{
      title:"更多服务",
      newClass:"nav-pb",
      items1:[
        {src:"./static/debit-icon1.png",alt:"小白卡",title:"小白卡",link:"/home"},
        {src:"./static/debit-icon2.png",alt:"白条商城",title:"白条商城",link:"/home"},
        {src:"./static/debit-icon3.png",alt:"消费分期",title:"消费分期",link:"/home"},
        {src:"./static/debit-icon4.png",alt:"借钱",title:"借钱",link:"/home"}
      ],
      items2:[
        {src:"./static/debit-icon5.png",alt:"白条提额",title:"白条提额",link:"/home"},
        {src:"./static/debit-icon6.png",alt:"白条闪付",title:"白条闪付",link:"/home"},
        {src:"./static/debit-icon7.png",alt:"小白会玩",title:"小白会玩",link:"/home"},
        {src:"./static/debit-icon8.png",alt:"钢镚",title:"钢镚",link:"/home"}
      ]
    }
  }
}
</script>
<style lang="less" type="text/less" scoped>
.banner{
display: block;
width: 100%;
 }

</style>



