<template>
  <div class="home-header" >
    <!-- <Swiper :items="items"></Swiper > -->
<Navbar :items="navs" class="home-nav" :newName="newName"></Navbar>
  </div>

</template>
<script>
// import Swiper from "../core/Swiper"
import Navbar from "../core/Navbar"
export default {
  name:"Slide",
  components:{
    Navbar,
    // Swiper
  },
  data(){
    return{
      newName:"red",
      navs:[
        {
          src:"./static/homebar1.png",
          alt:"no1",
          title:"优惠券",
          link:"/debit"
        },
        {
          src:"./static/homebar2.png",
          alt:"no2",
          title:"领红包",
          link:"/debit"
        },
        {
          src:"./static/homebar3.png",
          alt:"no3",
          title:"抢钢镚",
          link:"/debit"
        },
        {
          src:"./static/homebar4.png",
          alt:"no4",
          title:"白条提额",
          link:"/debit"
        }
      ],
      options:{
        speed:300
      },
      items:[
        {
          src:"~@/img/banner1.jpg",
          alt:"第一张图片",
          link:"/debit"
        },
        {
          src:"~@/img/banner2.jpg",
          alt:"第二张图片",
          link:"/debit"
        },
        {
          src:"~@/img/banner3.jpg",
          alt:"第三张图片",
          link:"/debit"
        },
        {
          src:"~@/img/banner4.jpg",
          alt:"第四张图片",
          link:"/debit"
        }
      ]

    }
  }
}
</script>
<style lang="less" type="text/less" scoped>
.home-header{
  margin-top: 100px;
  .home-nav{
    background-color: #fff;
    padding-top: 40px;
    margin-bottom: 20px;
  a{
        h4{color:red;}
}
      }
    }
</style>



