<template>
<div :class="[navbar,newName]">
  <router-link :to="item.link" tag="a" v-for="(item,index) in items" :key="item.src">
    <img :src="item.src" :alt="item.alt"/>
    <h4>{{item.title}}</h4>
</router-link>
</div>
</template>
<script>
export default {
  name:"navbar",
  props:{
    newName:{
      type:String,
      default:""
    },
    items:{
      type:Array,
      default(){
        return[{src:"",alt:"",title:"",link:"/"}]
      }
    }
  },
  data(){
    return{
      navbar:"navbar",
    }
  }
}
</script>
<style lang="less" type="text/less">
@import "../../style/element";
.navbar{
  .navbar();
  .active h4{
    color:#f00;
  }
}
.red{
  a:nth-of-type(3){
    h4{color:#f00;}
  }
}
.bottom-nav{
  a{
    img{
      width: 44px;
      height: 44px;
    }
  }
}
.home-mix-banner{
  padding-bottom:40px;
  a{
    img{
      display: block;
      width: 100%;
      height:auto;
    }
  }
}
.debit-more-server{
  a{
    width: 25%;
  }
}
.nav-pt{
padding-top:40px;
}
.nav-pb{
padding-bottom: 40px;
}
</style>



