<template>
  <Panel :title="title">
    <section class="product-list">
      <div v-for="product in products" class="product">
        <h5>{{product.name}}<span>{{product.feature}}</span></h5>
        <p class="rate">{{product.rate}}</p>
        <p class="intro">{{product.intro}}</p>
      </div>
    </section>
  </Panel>
</template>
<script>
import Panel from "../core/Panel"
export default {
  name:"Financing",
  components:{
    Panel
  },
  data(){
    return{
      title:"精选推荐",
      products:[
        {
          name:"定期理财",
          feature:"90天可质押",
          rate:"5.50%",
          intro:"历史年化收益率"
        },
        {
          name:"年年盈",
          feature:"理财推荐",
          rate:"5.80%",
          intro:"综合年化收益率"
        },
        {
          name:"基智账户",
          feature:"组合投资",
          rate:"8%-10%",
          intro:"止盈年化收益率"
        },
        {
          name:"小白基金",
          feature:"超短期",
          rate:"5.00%",
          intro:"7日年化收益率"
        }
      ]
    }
  }
}
</script>

<style lang="less" type="text/less" scoped>
.bold(){font-weight: 700}
.product-list{
  display: flex;
  flex-flow: row wrap;
  @bgc:#eee;
  &::before{
    content: "";
    width: 100%;
    height: 1px;
    transform: scaleY(0.5) translateY(436px);
    background-color: @bgc;
  }
  .product{
    width: 50%;
    box-sizing: border-box;
    padding-left: 32px;
    height: 218px;
    position: relative;
    &:nth-of-type(2n+1){
      &::after{
          content:"";
          width: 1px;
          height: 136px;
          background-color:@bgc;
          position: absolute;
          right: 0;
          top: 41px;
          transform: scaleX(.5);
      }
    }
    @red:#ff5155;
    h5{
    color:#333;
    margin-top: 34px;
    margin-bottom:16px;
    font-size: 30px;
    .bold;
    line-height: 42px;
      span{
        font-weight: normal;
        font-size: 22px;
        line-height: 42px;
        padding: 0 8px;
        margin-left: 8px;
        color: @red;
        display: inline-block;
        position: relative;
        &::before{
        content: "";
        border:1px solid @red;
        position: absolute;
        width: 100%;
        height: 34px;
        left:-4px;
        top:2px;
      }
    }}
    .rate{
      .bold;
      font-size: 44px;
      line-height: 58px;
      color:@red;
    }
    .intro{
      font-size: 24px;
      line-height: 34px;
      color:#999;
      white-space: normal;
      text-overflow: ellipsis;
      overflow: hidden;
    }
  }
}
</style>



