<template>
  <Panel :title="title">
    <router-link slot="title-right" class="look-more" tag="a" :to="{name:'Home'}">更多尖货 </router-link>
  </Panel>
</template>
<script>
  import Panel from "../core/Panel"
export default {
  name:"Crowdfunding",
  components:{
    Panel
  },
  data(){
    return{
      title:"众筹新品"
    }
  }
}
</script>
<style lang="less" type="text/less" scoped>
.look-more{
position: absolute;
color: #999;
right:32px;
line-height: 112px;
display: inline-block;
padding:0 32px;
font-size: 28px;
background: url("~@/assets/icon_arrow1.png") center right no-repeat;
background-size: 18px 28px;
}
</style>



