<template>
<div :class="[panel,newName]">
  <slot name="title-right"></slot>
  <h4 class="title"> -{{title}}-</h4>
  <slot name="default"></slot>
</div>
</template>
<script>
export default {
  name:"panel",
  props:{
    title:{
      type:String,
      default:""
    },
    newName:{
      type:String,
      default:""
    }
  },
  data(){
    return{
      panel:"panel"
    }
  }
}
</script>
<style lang="less" type="text/less">
@import "../../style/element";
.panel{
.panel
}
.panel-padding{
padding-bottom:40px;
}
</style>



