<template>
  <Panel :title="title">
    <section class="borrow-weal">
      <div v-for="product in products" class="product">
        <img class="product-icon" :src="product.src"/>
        <h4 class="product-title">{{product.name}}</h4>
        <p class="product-feature">
          {{product.feature}}
        </p>
        <router-link class="product-links" tag="a" :to="product.link">{{product.ref}}</router-link>
      </div>
    </section>
  </Panel>
</template>
<script>
import Panel from "../core/Panel"
export default {
  name:"Financing",
  components:{
    Panel
  },
  data(){
    return{
      title:"白条福利",
      products:[
        {
          name:"开通白条",
          feature:"送百元礼包",
          src:"./static/take.jpg",
          link:"/",
          ref:"立即查看"
        },
        {
          name:"白条提额",
          feature:"金融app专享提额礼包",
          src:"./static/give.jpg",
          link:"/",
          ref:"去提额"
        }

      ]
    }
  }
}
</script>

<style lang="less" type="text/less" scoped>
.borrow-weal{
  position: relative;
      &::before{
        content: "";
        width: 100%;
        position: absolute;
        top: 0;
        height: 1px;
        background-color: #eee;
    }
  .product{
    position: relative;
    padding: 0 32px 0 152px;
    height: 140px;
    &::before{
      content: "";
      width: 100%;
      height: 1px;
      background-color: #eee;
      display: block;
      transform: scaleY(.5);
    }
    .product-icon{
      position: absolute;
      height: 88px;
      width: 88px;
      left: 32px;
      top:26px;
    }
    .product-title{
      font-size: 32px;
      color:#333;
      line-height: 1.8;
      padding-top:26px;
    }
    .product-feature{
        color: #999;
        font-size: 24px;
        max-width: 48%;
        white-space: nowrap;
        text-overflow: ellipsis;
        overflow: hidden;
    }
    .product-links{
      position: absolute;
      right:32px;
      font-size: 28px;
      top: 0;
      line-height: 140px;
      color:#ff801a;
      padding-right:36px;
      background: url("~@/assets/icon_arrow1.png") no-repeat right center;
    }
}

}
</style>



