import Vue from 'vue'
import Router from 'vue-router'
import Head from '@/components/public/Head'
import Foot from '@/components/public/Foot'
import Wrong from '@/components/public/Wrong'
import Home from '@/components/home'
import Money from '@/components/money'
import Debit from '@/components/debit'
import Mine from '@/components/mine'

Vue.use(Router)

export default new Router({
  linkActiveClass: 'links',
  linkExactActiveClass: 'active',
  routes: [
    {
      path: '/',
      name: 'home',
      components: {
        top:Head,
        middle:Home,
        bottom:Foot
      }
    },
    {
      path: '/home',
      name: 'Home2',
      components: {
        top:Head,
        middle:Home,
        bottom:Foot
      }
    },
    {
      path: '/money',
      name: 'Money',
      components: {
        top:Head,
        middle:Money,
        bottom:Foot
      }
    },
    {
      path: '/debit',
      name: 'Debit',
      components: {
        top:Head,
        middle:Debit,
        bottom:Foot
      }
    },
    {
      path: '/mine',
      name: 'Mine',
      components:{top:Mine}
    },
    {
      path: '/404',
      name: 'Wrong',
      components:{top:Wrong}
    }
  ]
})



